import { useWeb3React } from "@web3-react/core";
import LeaderBoard from "./LeaderBoard";
import { ToastContainer, toast } from "react-toastify";
import { useEffect } from "react";

function l(what) {
  return document.getElementById(what);
}

var Pic = function (what) {
  if (Game.Loader.assetsLoaded.indexOf(what) != -1) {
    return Game.Loader.assets[what];
  } else if (Game.Loader.assetsLoading.indexOf(what) == -1)
    Game.Loader.Load([what]);
  return Game.Loader.blank;
};

function AddEvent(el, ev, func) {
  if (el.addEventListener) {
    el.addEventListener(ev, func, false);
    return [el, ev, func];
  } else if (el.attachEvent) {
    var func2 = function () {
      func.call(el);
    };
    el.attachEvent("on" + ev, func2);
    return [el, ev, func2];
  }
  return false;
}

var Loader = function () //asset-loading system
{
  this.loadingN = 0;
  this.assetsN = 0;
  this.assets = [];
  this.assetsLoading = [];
  this.assetsLoaded = [];
  this.domain = "img/";
  this.loaded = 0; //callback
  this.doneLoading = 0;

  this.blank = document.createElement("canvas");
  this.blank.width = 8;
  this.blank.height = 8;
  this.blank.alt = "blank";

  this.Load = function (assets) {
    for (var i in assets) {
      this.loadingN++;
      this.assetsN++;
      if (
        this.assetsLoading.indexOf(assets[i]) == -1 &&
        this.assetsLoaded.indexOf(assets[i]) == -1
      ) {
        var img = new Image();
        if (!Game.local) img.crossOrigin = "anonymous";
        img.alt = assets[i];
        var self = this;
        img.onload = function (e) {
          self.assetsLoaded.push(e.target.alt);
          self.assetsLoading.splice(
            self.assetsLoading.indexOf(e.target.alt),
            1
          );
          self.loadingN--;
          if (self.doneLoading == 0 && self.loadingN <= 0 && self.loaded != 0) {
            self.doneLoading = 1;
          }
        };
        self.assets[assets[i]] = img;
        self.assetsLoading.push(assets[i]);
        // if (assets[i].indexOf("/") != -1) img.src = assets[i];
        // else
        img.src = self.domain + assets[i];
      }
    }
  };
};

var Game = {
  ready: false,
  BigCookieSize: 0,
  BigCookieSizeT: 1,
  BigCookieSizeD: 0,
  fps: 10,
  cookieClicks: 0,
  lastClick: 0,
  handmadeCookies: 0,
  computedMouseCps: 1,
  cookies: 0,
  particles: [],
  cursors: 10,
  cookieOriginX: 0,
  cookieOriginY: 0,
  logicTime: Date.now(),
  loopTime: Date.now(),
  users: [],
  leaderBoardPageNum: 0,
  leaderBoardPageSize: 10,
  leaderBoardPageCount: 0,
};

Game.T = 0;
Game.OnAscend = 0;
Game.AscendTimer = 0; //how far we are into the ascend animation
Game.AscendDuration = Game.fps * 5; //how long the ascend animation is
Game.AscendBreakpoint = Game.AscendDuration * 0.5; //at
Game.milkProgress = 0; //you gain a little bit for each achievement. Each increment of 1 is a different milk displayed.
Game.milkH = Game.milkProgress / 2;
Game.milkHd = 0;
Game.cookiesEarned = 0;
Game.prefs = { fancy: 1 };
Game.mouseX2 = 0;
Game.mouseY2 = 0;
Game.mouseX = 0;
Game.mouseY = 0;
Game.scene = {
  x: 0,
  y: 0,
  w: 0,
  h: 0,
};

Game.showCoookies = function () {
  l("cookies").innerHTML = `${Game.handmadeCookies} cookies`;
};

Game.Launch = function () {
  Game.Load = function (callback) {
    Game.Loader = new Loader();
    callback();
  };

  Game.login = function (isActive, account) {
    Game.socket.emit("req-login", { address: account || false });
  };

  Game.logout = function (account) {
    Game.socket.emit("req-logout", { address: account || false });
    Game.ready = false;
  };

  Game.Init = function () {
    var bigCookie = l("bigCookie");

    AddEvent(bigCookie, "mousedown", function (event) {
      Game.BigCookieState = 1;
      Game.ClickCookie(event);
    });
    AddEvent(bigCookie, "mouseup", function (event) {
      Game.BigCookieState = 2;
    });
    AddEvent(bigCookie, "mouseout", function (event) {
      Game.BigCookieState = 0;
    });
    AddEvent(bigCookie, "mouseover", function (event) {
      Game.BigCookieState = 2;
    });
    AddEvent(document, "mousemove", Game.GetMouseCoords);

    AddEvent(bigCookie, "touchstart", function (event) {
      Game.BigCookieState = 0;
      if (event) event.preventDefault();
    });
    AddEvent(bigCookie, "touchend", function (event) {
      Game.BigCookieState = 2;

      Game.GetMouseCoords(event.changedTouches[0]);
      Game.ClickCookie(event.changedTouches[0]);
      if (event) event.preventDefault();
    });

    Game.interval = setInterval(Game.Loop, Game.fps);

    Game.getUsers();
    // setTimeout(() => {
    //   l("offGameMessageWrap").style.display = "none";
    // }, 1000);
  };

  Game.getUsers = function () {
    Game.socket.emit("req-users", {});
  };

  Game.GetMouseCoords = function (e) {
    var posx = 0;
    var posy = 0;
    if (!e) var e = window.event;
    if (e.pageX || e.pageY) {
      posx = e.pageX;
      posy = e.pageY;
    } else if (e.clientX || e.clientY) {
      posx =
        e.clientX +
        document.body.scrollLeft +
        document.documentElement.scrollLeft;
      posy =
        e.clientY +
        document.body.scrollTop +
        document.documentElement.scrollTop;
    }
    var x = 0;
    var y = 0;
    /*
			var el=l('sectionLeft');
			while(el && !isNaN(el.offsetLeft) && !isNaN(el.offsetTop))
			{
				x+=el.offsetLeft-el.scrollLeft;
				y+=el.offsetTop-el.scrollTop;
				el=el.offsetParent;
			}*/

    var position = l("backgroundLeftCanvas").getBoundingClientRect();

    Game.mouseX2 = Game.mouseX;
    Game.mouseY2 = Game.mouseY;
    Game.mouseX = posx - x;
    Game.mouseY = posy - y;
    Game.scene = {
      x: position.x,
      y: position.y,
      w: position.width,
      h: position.height,
    };
    // console.log(Game.mouseX - position.x, Game.mouseY - position.y);
    // Game.mouseMoved = 1;
    // Game.lastActivity = Game.time;
  };

  Game.ClickCookie = function (e, amount) {
    if (!Game.ready) return;

    var now = Date.now();

    if (!e || now - Game.lastClick < 100) {
      return;
    }

    if (e && typeof e.target === "undefined") {
      return;
    }

    Game.lastClick = now;

    var amount = amount ? amount : Game.computedMouseCps;
    Game.Earn(amount);
    Game.handmadeCookies += amount;

    Game.particleAdd(
      Game.mouseX,
      Game.mouseY,
      Math.PI + getRndInteger(-2, 2) / 10,
      1,
      e
    );

    Game.socket.emit("cookie-clicker");
    Game.cookieClicks++;
  };

  Game.Logic = function () {
    if (Game.BigCookieState == 1) Game.BigCookieSizeT = 0.98;
    else if (Game.BigCookieState == 2) Game.BigCookieSizeT = 1.05;
    else Game.BigCookieSizeT = 1;
    Game.BigCookieSizeD += (Game.BigCookieSizeT - Game.BigCookieSize) * 0.75;
    Game.BigCookieSizeD *= 0.75;
    Game.BigCookieSize += Game.BigCookieSizeD;
    Game.BigCookieSize = Math.max(0.1, Game.BigCookieSize);

    Game.milkProgress = Game.cookiesEarned;
    Game.milkH = Math.min(1, Game.milkProgress) * 0.35;
    Game.milkHd += (Game.milkH - Game.milkHd) * 0.02;
    Game.particleUpdate();
    Game.showCoookies();

    // var dt = Date.now();
    // var len = Math.floor((Game.cursors / 8000) * (dt - Game.logicTime));
    // if (len > 0) {
    //   Game.logicTime = dt;
    // }
    // for (var i = 0; i < len; i++) {
    //   Game.ClickCookie();
    // }
    Game.T++;
    Game.MilkProTime += 3;
  };

  Game.Loop = function () {
    var dt = Date.now();
    var count = Math.floor((dt - Game.loopTime) / 30);
    for (var i = 0; i < count; i++) {
      Game.Logic();
    }
    if (count >= 1) {
      Game.loopTime = dt;
    }
    Game.Draw();
  };

  Game.Earn = function (howmuch) {
    Game.cookies += howmuch;
    Game.cookiesEarned += howmuch;
  };

  Game.Draw = function () {
    Game.DrawBackground();
    Game.DrawbigCookie();
    Game.DrawCursors();
    Game.DrawMilk();
    Game.DrawParticles();
  };

  Game.DrawBackground = function () {
    if (!Game.Background) {
      Game.Background = l("backgroundCanvas").getContext("2d");
      Game.Background.canvas.width =
        Game.Background.canvas.parentNode.offsetWidth;
      Game.Background.canvas.height =
        Game.Background.canvas.parentNode.offsetHeight;
      Game.gamecanvas = l("backgroundLeftCanvas").getContext("2d");
      Game.gamecanvas.canvas.width =
        Game.gamecanvas.canvas.parentNode.offsetWidth;
      Game.gamecanvas.canvas.height =
        Game.gamecanvas.canvas.parentNode.offsetHeight;
      Game.gamecanvas.globalAlpha = 0;
      Game.gamecanvas.drawImage(Pic("brokenCookie.png"), 0, 0);
      Game.gamecanvas.drawImage(Pic("brokenCookieHalo.png"), 0, 0);

      var Resize = function (event) {
        Game.Background.canvas.width =
          Game.Background.canvas.parentNode.offsetWidth;
        Game.Background.canvas.height =
          Game.Background.canvas.parentNode.offsetHeight;
        Game.gamecanvas.canvas.width =
          Game.gamecanvas.canvas.parentNode.offsetWidth;
        Game.gamecanvas.canvas.height =
          Game.gamecanvas.canvas.parentNode.offsetHeight;

        Game.cookieOriginX = Math.floor(Game.gamecanvas.canvas.width / 2);
        Game.cookieOriginY = Math.floor(Game.gamecanvas.canvas.height * 0.4);
      };

      window.addEventListener("resize", Resize);
      Resize();
    }

    var ctx = Game.Background;

    var ctx1 = Game.gamecanvas;

    ctx1.clearRect(
      0,
      0,
      Game.gamecanvas.canvas.width,
      Game.gamecanvas.canvas.height
    );

    ctx.clearRect(
      0,
      0,
      Game.Background.canvas.width,
      Game.Background.canvas.height
    );

    // var pat = ctx.createPattern(Pic("bgBlue.jpg"), "repeat");
    // ctx.fillStyle = pat;
    // ctx.fillRect(
    //   0,
    //   0,
    //   Game.Background.canvas.width,
    //   Game.Background.canvas.height
    // );
  };

  Game.DrawbigCookie = function () {
    var ctx = Game.gamecanvas;

    var x = ctx.canvas.width / 2;
    var y = ctx.canvas.height * 0.4;
    var s = 256 * Game.BigCookieSize;

    ctx.save();
    ctx.drawImage(Pic("cookieShadow.png"), x - s / 2, y - s / 2 + 20, s, s);
    ctx.translate(x, y);
    Game.DrawSpecial();

    ctx.drawImage(Pic("perfectCookie.png"), -s / 2, -s / 2, s, s);
    ctx.restore();
  };

  Game.DrawCursors = function () {
    return;
    var ctx = Game.gamecanvas;
    ctx.save();
    ctx.translate(Game.cookieOriginX, Game.cookieOriginY);
    var pic = Pic("cursor.png");
    var fancy = Game.prefs.fancy;
    for (var i = 0; i < Game.cursors; i++) {
      var n = Math.floor(i / 50);
      var w = 0;
      if (fancy)
        w = Math.sin(Game.T * 0.025 + (((i + n * 12) % 25) / 25) * Math.PI * 2);

      if (w > 0.997) {
        w = 1.5;
      } else if (w > 0.994) {
        w = 0.5;
      } else {
        w = 0;
      }
      w *= -4;
      if (fancy) w += Math.sin(((n + Game.T * 0.01) * Math.PI) / 2) * 4;
      var x = 0;
      var y = 140 /* *Game.BigCookieSize*/ + n * 16 + w - 16;

      var rot = 7.2; //(1/50)*360
      if (i == 0 && fancy) rot -= Game.T * 0.1;
      if (i % 50 == 0) rot += 7.2 / 2;
      ctx.rotate((rot / 360) * Math.PI * 2);
      ctx.drawImage(pic, 0, 0, 32, 32, x, y, 32, 32);
    }
    ctx.restore();
  };

  Game.DrawSpecial = function () {
    var ctx = Game.gamecanvas;
    var ss = 430;
    var r = Math.floor(Game.T % 360);
    ctx.save();
    ctx.rotate((r / 360) * Math.PI * 2);
    ctx.globalAlpha = 0.3;
    ctx.drawImage(Pic("shine.png"), -ss / 2, -ss / 2, ss, ss);
    ctx.restore();

    ctx.save();
    ctx.rotate(-(r / 360) * Math.PI * 2);
    ctx.globalAlpha = 0.3;
    ctx.drawImage(Pic("shine.png"), -ss / 2, -ss / 2, ss, ss);
    ctx.restore();
  };
};

Game.MilkProTime = 0;

Game.DrawMilk = function () {
  var ctx = Game.gamecanvas;
  var x = Math.floor(Game.MilkProTime * 2) - 20;
  var height = (1 + Game.milkHd) * 100;
  var y = ctx.canvas.height - height + 60;
  var width = ctx.canvas.width;

  if (x > width) {
    Game.MilkProTime = 0;
  }
  ctx.save();
  ctx.globalAlpha = 0.65;
  ctx.drawImage(Pic("milkPlain.png"), x, y, width + 30, height);
  ctx.drawImage(Pic("milkPlain.png"), x - width - 30, y, width + 30, height);
  ctx.restore();
};

Game.particleAdd = function (x, y, r, text, e) {
  if (e) {
    Game.particles.push({
      type: "small-cookier",
      pos: { x, y },
      r: r,
      speed: 3,
      time: 40,
      size: getRndInteger(30, 40),
    });

    Game.particles.push({
      type: "text",
      pos: { x: x + getRndInteger(0, 20), y: y - 10 },
      speed: 1,
      text,
      time: 150,
    });
  }

  Game.particles.push({
    type: "back-cookier",
    pos: { x: getRndInteger(0, Game.gamecanvas.canvas.width - 30), y: -30 },
    speed: 5,
    time: 130,
    size: getRndInteger(30, 50),
  });
};

Game.particleUpdate = function () {
  for (var i = 0; i < Game.particles.length; i++) {
    var prt = Game.particles[i];
    switch (prt.type) {
      case "small-cookier":
        prt.pos.x += Math.sin(prt.r) * 6;
        prt.pos.y += Math.cos(prt.r) * prt.speed;
        prt.speed -= 0.2;
        prt.time--;
        if (prt.time < 0) Game.particles.splice(i, 1);
        break;

      case "text":
        prt.pos.y -= prt.speed;
        prt.time--;
        if (prt.time < 0) Game.particles.splice(i, 1);
        break;
      case "back-cookier":
        prt.pos.y += prt.speed;
        prt.time--;
        if (prt.time < 0) Game.particles.splice(i, 1);
        break;
    }
  }
};

Game.DrawParticles = function (z) {
  var ctx = Game.gamecanvas;
  var ctx1 = Game.Background;
  ctx.fillStyle = "#fff";
  ctx.font = "bold 25px Merriweather";
  ctx.strokeStyle = "#fff";
  ctx.textAlign = "center";
  for (var i = 0; i < Game.particles.length; i++) {
    var prt = Game.particles[i];
    if (prt)
      switch (prt.type) {
        case "small-cookier":
          ctx.save();
          ctx.translate(prt.pos.x - Game.scene.x, prt.pos.y - Game.scene.y);
          ctx.globalAlpha = (1 / 40) * prt.time;
          ctx.drawImage(Pic("perfectCookie.png"), -20, -20, prt.size, prt.size);
          ctx.restore();
          break;
        case "text":
          ctx.save();

          ctx.globalAlpha = (1 / 150) * prt.time;
          ctx.translate(prt.pos.x - Game.scene.x, prt.pos.y - Game.scene.y);
          ctx.fillText("+" + prt.text, 0, -50);
          ctx.stroke();

          ctx.restore();
          break;
        case "back-cookier":
          ctx1.save();
          ctx1.globalAlpha = (1 / 130) * prt.time;
          ctx1.drawImage(
            Pic("perfectCookie.png"),
            prt.pos.x + Game.scene.x || 0,
            prt.pos.y || 0,
            prt.size,
            prt.size
          );
          ctx1.restore();
          break;
      }
  }
};

Game.onSocket = function () {
  Game.socket = document.socket;
  Game.socket.on("req-login", function () {
    // if (Game.ready) {
    Game.login && Game.login();
    // Game.ready = false;
    // }
  });

  Game.socket.on("res-login", function ({ success, clickcount, message }) {
    if (success) {
      Game.handmadeCookies = clickcount;
      Game.cursors = Math.floor(Math.sqrt(clickcount) / 4);
      Game.ready = true;
      if (!localStorage.getItem("isConnected")) {
        localStorage.setItem("isConnected", true);
      }
    } else {
      toast.error(<div className="toast-content">{message}</div>);
      Game.ready = false;
    }
  });

  Game.socket.on("users", function (users) {
    if (document.Game.onUserData) {
      document.Game.onUserData(users);
    }
  });

  Game.socket.on("res-users", function (users, pageInfo) {
    Game.leaderBoardPageNum = pageInfo.pageNum;
    Game.leaderBoardPageSize = pageInfo.pageSize;
    Game.leaderBoardPageCount = pageInfo.pageCount;
    Game.users = users;
  });

  Game.socket.on("req-users", function () {
    Game.getUsers();
  });
};

Game.runed = false;

document.Game = Game;
function getRndInteger(min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

Game.onSocket();

export default function () {
  useEffect(() => {
    if (!Game.ready && !Game.runed) {
      Game.runed = true;

      Game.Launch();
      Game.Load(Game.Init);
    }
  }, []);

  return (
    <div id="wrapper">
      <div id="game">
        <canvas id="backgroundCanvas"></canvas>
        <div className="scene">
          <div className="separatorLeft" id="leftBeam"></div>
          <div id="sectionLeft" className="inset">
            <canvas id="backgroundLeftCanvas" style={{ zIndex: 5 }}></canvas>
            <div className="blackGradient"></div>
            <div id="sectionLeftInfo"></div>
            <div id="cookies"></div>
            <div id="cookieAnchor">
              <button id="bigCookie"></button>
              <div id="cookieNumbers"></div>
            </div>
            <div id="sectionLeftExtra"></div>
          </div>

          <div className="separatorRight" id="rightBeam"></div>
        </div>
        <div className="leader-board-wrapper">
          <LeaderBoard></LeaderBoard>
        </div>
      </div>
    </div>
  );
}
