import React, { useEffect, useState } from "react";
import "./leaderboard.style.css";

document.copyTextContent = "";
export default function () {
  const [_users, setUsers] = useState([]);

  //   const [, setLank] = useState(0);
  const nextPage = () => {
    document.Game.leaderBoardPageNum++;
    document.Game.getUsers && document.Game.getUsers();
  };

  const preventPage = () => {
    document.Game.leaderBoardPageNum--;
    document.Game.getUsers && document.Game.getUsers();
  };
  const getnavigation = () => {
    var i =
      document.Game.leaderBoardPageCount / document.Game.leaderBoardPageSize;
    // i = i < 1 ? 1 : i;
    var map = [];
    map.push(
      <button
        disabled={document.Game.leaderBoardPageNum === 0}
        onClick={preventPage}
        key={0}
      >
        {"<"}
      </button>
    );
    // for (var j = 0; j < i; j++) {
    map.push(
      <button disabled={true} key={1}>
        {document.Game.leaderBoardPageNum + 1}
      </button>
    );
    // }
    map.push(
      <button
        disabled={document.Game.leaderBoardPageNum >= Math.floor(i)}
        key={2}
        onClick={nextPage}
      >
        {">"}
      </button>
    );
    return map;
  };
  useEffect(() => {
    if (document.Game) {
      document.Game.onUserData = function (users) {
        setUsers(
          document.Game.users.map((user) => {
            var u = users.find(({ address }) => address == user.walletAddress);
            user.handmadeCookies = u ? u.cookies : user.handmadeCookies;
            return {
              address: user.walletAddress,
              cookies: user.handmadeCookies,
              status: u ? true : false,
            };
          })
        );
      };
    }

    document.addEventListener("copy", function (event) {
      event.preventDefault();
      if (event.clipboardData) {
        event.clipboardData.setData("text/plain", document.copyTextContent);
      }
    });
  }, []);

  return (
    <div className="leader-board">
      <div style={{ fontSize: "30px" }} className="board-title">
        LEADERBOARD
      </div>
      <div className="board-header">
        <div className="leader-item-cell w-1">#</div>
        <div className="leader-item-cell w-4">ADDRESS</div>
        <div className="leader-item-cell w-3">COOKIES</div>
        <div className="leader-item-cell w-1"></div>
      </div>
      <div className="board-body">
        {_users
          .sort((a, b) => b.cookies - a.cookies)
          .map((user, i) => {
            var lanknum =
              document.Game.leaderBoardPageCount *
              document.Game.leaderBoardPageNum;

            return (
              <div className="leader-item-row" key={i}>
                <div className="leader-item-cell w-1 ">{i + 1 + lanknum}</div>
                <div
                  className="leader-item-cell w-4  "
                  style={{
                    cursor: "pointer",
                    display: "flex",
                    flexDirection: "row",
                  }}
                >
                  <div
                    className="tooltip"
                    style={{ color: "green" }}
                    onCopy={() => {}}
                  >
                    {user.address.substring(0, 4) +
                      "..." +
                      user.address.substring(
                        user.address.length - 4,
                        user.address.length
                      )}
                    <span className="tooltiptext">{user.address}</span>
                  </div>

                  <div
                    className="tooltip copy-btn"
                    onClick={(event) => {
                      document.copyTextContent = user.address;
                      document.execCommand("copy");
                    }}
                    style={{ paddingLeft: "3px" }}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="17"
                      height="17"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-copy"
                    >
                      <rect
                        x="9"
                        y="9"
                        width="13"
                        height="13"
                        rx="2"
                        ry="2"
                      ></rect>
                      <path d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"></path>
                    </svg>
                    <span className="tooltiptext">Copy</span>
                  </div>
                </div>
                <div className="leader-item-cell w-3">{user.cookies}</div>
                <div className="leader-item-cell w-1">
                  <div
                    className={user.status ? "badge-online" : "badge-block"}
                  ></div>
                </div>
              </div>
            );
          })}
      </div>
      <div className="board-fooder"></div>
    </div>
  );
}
