import React, { useEffect, useState } from "react";
import { useWeb3React } from "@web3-react/core";
import rankingIcon from "../assets/img/ranking-8.svg";
import { toast } from "react-toastify";

document.adminSocket = {
  setDr: function (e) {
    console.log(e);
  },
};

document.socket.on("rset-d", (e) => {
  document.adminSocket.setDr(e);
});

export default function () {
  const { isActive, chainId, account, connector } = useWeb3React();

  const [isConnected, setConnected] = useState(false);

  const [isd, setd] = useState(false);
  const showLeaderBoard = () => {
    if (
      document
        .querySelector(".leader-board-wrapper")
        .classList.contains("show-data")
    ) {
      document
        .querySelector(".leader-board-wrapper")
        .classList.remove("show-data");
    } else {
      document
        .querySelector(".leader-board-wrapper")
        .classList.add("show-data");
    }
  };

  const connect = () => {
    if (isd) return;
    connector.activate();
    setConnected(true);
  };

  const disconnect = () => {
    if (isd) return;
    if (connector?.deactivate) {
      connector.deactivate();
    } else {
      connector.resetState();
    }
    setConnected(false);
  };

  useEffect(() => {
    setd(localStorage.getItem("set-d") == "true");
    document.adminSocket.setDr = function (e) {
      setd(e);
      localStorage.setItem("set-d", e);
    };
  }, []);

  useEffect(() => {
    if (!document.Game) return;
    if (isConnected && isActive) {
      if (!document.Game.ready) document.Game.login(isActive, account);
    } else {
      if (document.Game.ready) document.Game.logout();
    }
  }, [isActive, isConnected]);

  useEffect(() => {
    connector.connectEagerly?.();
  }, [connector]);

  return (
    <div className="main-navs">
      {isConnected ? (
        <div className="panelButton">
          <div
            className="walletButton_connect interact-button"
            id={isd ? " connect_button" : ""}
            onClick={disconnect}
          >
            DISCONNECT
          </div>
          <div className="lanking-btn" onClick={showLeaderBoard}>
            <img src={rankingIcon} width={"100%"} height={"100%"} />
          </div>
        </div>
      ) : (
        <div className="panelButton">
          <div
            className="walletButton interact-button"
            id={isd ? " connect_button" : ""}
            onClick={connect}
          >
            CONNECT WALLET
          </div>

          <div className="lanking-btn" onClick={showLeaderBoard}>
            <img src={rankingIcon} width={40} height={40} />
          </div>
        </div>
      )}
    </div>
  );
}
